<template>
  <div class="container">
    <br />
    <br />
    <br />
    <div class="row justify-content-center">
      <div class="col-md-8">
        <div class="card">
          <div class="card-header">Login</div>
          <div class="card-body">
            <div v-if="error" class="alert alert-danger">{{error}}</div>
            <form action="#" @submit.prevent="login">
              <div class="form-group row">
                <label for="email" class="col-md-4 col-form-label text-md-right">Username</label>

                <div class="col-md-6">
                  <input
                    id="username"
                    type="username"
                    class="form-control"
                    name="username"
                    value
                    required
                    autofocus
                    v-model="vdata.username"
                  />
                </div>
              </div>

              <div class="form-group row">
                <label for="password" class="col-md-4 col-form-label text-md-right">Password</label>

                <div class="col-md-6">
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="vdata.password"
                  />
                </div>
              </div>

              <div class="form-group row mb-0">
                <div class="col-md-12 text-center">
                  <button type="submit" class="btn btn-primary font-times">
                    <span class="typcn typcn-key"></span> Login
                  </button>
                </div>
              </div>
              <br />
              <!-- <p class="text-center font-times">Atau login menggunakan</p> -->
              <br />
            </form>
            <!-- <button type="button" @click="facebookAuth" class="btn btn-sm btn-outline-dark text-green btn-block  "><span class="typcn typcn-social-facebook-circular"></span> Facebook</button> -->
            <br />
            <br />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import autophp from '@/plugins/autophp_gis';
let sdb = new autophp();
export default {
  data() {
    return {
      vdata: {
        email: "",
        password: ""
      },
      cordova: false,
      error: null
    };
  },
  methods: {
      login(){
           sdb.collection('tbuser').login2(this.vdata,this.$store,this.$router,'/app/covid/admin').then(res=>{
                console.log(res)
            })
      }
  },
  mounted() {
   
  }
};
</script>